import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import HomepageLight from "./pages/HomepageLight";
import "./App.scss";
import PaperDetails from "./pages/PaperDetails";

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" exact>
          <HomepageLight />
        </Route>
        <Route path="/papers/paper-details/:id/:title" component={PaperDetails} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
