import React from "react";
import ScrollAnimation from "react-animate-on-scroll";
import {Link} from "react-router-dom";

function Blog({ blogData }) {
  const { id, link, title, image, desc } = blogData;
  return (
    <ScrollAnimation
      animateIn="fadeInUp"
      animateOut="fadeInOut"
      animateOnce={true}
    >
      <div className="blog-item rounded bg-white shadow-dark">
        <div className="thumb">
          <a href={link}>
            <img style={{objectFit: 'contain', width: "350px", height: "250px"}} src={image} alt="blog-title" />
          </a>
        </div>
        <div className="details">
          <h4 className="my-0 title">
            <a href={link}>
              {title}
            </a>
          </h4>
          <p>
            {desc}
          </p>
        </div>
      </div>
    </ScrollAnimation>
  );
}

export default Blog;
